export var Direction;
(function(Direction) {
    Direction[Direction["ASCENDING"] = 0] = "ASCENDING";
    Direction[Direction["DESCENDING"] = 1] = "DESCENDING";
})(Direction || (Direction = {}));
export class SortService {
    static options = [];
    static sort(arrayToBeSorted, options) {
        SortService.options = options;
        return arrayToBeSorted.sort(SortService.compare);
    }
    static compare(object1, object2) {
        if (SortService.options === Direction.ASCENDING) {
            if (object1 < object2) {
                return -1;
            }
            if (object1 > object2) {
                return 1;
            }
        } else if (SortService.options === Direction.DESCENDING) {
            if (object1 > object2) {
                return -1;
            }
            if (object1 < object2) {
                return 1;
            }
        } else {
            for (const sortByEntry of SortService.options){
                if (object1[sortByEntry.fieldName] === object2[sortByEntry.fieldName]) {
                    continue;
                }
                if (sortByEntry.direction === Direction.ASCENDING) {
                    if (object1[sortByEntry.fieldName] < object2[sortByEntry.fieldName]) {
                        return -1;
                    }
                    if (object1[sortByEntry.fieldName] > object2[sortByEntry.fieldName]) {
                        return 1;
                    }
                } else if (sortByEntry.direction === Direction.DESCENDING) {
                    if (object1[sortByEntry.fieldName] > object2[sortByEntry.fieldName]) {
                        return -1;
                    }
                    if (object1[sortByEntry.fieldName] < object2[sortByEntry.fieldName]) {
                        return 1;
                    }
                }
            }
        }
        return 0;
    }
}
